body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.modal .modal-dialog .modal-content {
  padding: 10px;
}

ul.pokelist-container li .pokemon-container {
  padding-bottom: 20px;
}

ul.pokelist-container li .pokemon-container p {
  margin-bottom: 5px;
}

.modal-dialog.modal-sm form label {
  width: 100%;
}

.modal-dialog.modal-sm form label::after {
  content: "";
  display: block;
  clear: both;
}

.modal-dialog.modal-sm form input {
  float: right;
}

/* add form */

body {
  position: relative;
}

.add-new-form-container {
  position: fixed;
  top: 0;
  right: 0;
  padding: 10px;
}

.add-new-form-container input, .add-new-form-container textarea, .add-new-form-container button {
  width: 100%;
}

.add-new-form-container textarea {
  padding: 10px;
  border-radius: 6px;
}

.add-new-form-container button {
  margin-top: 15px;
}

/* search input */
div.search-input {
  width: 100%;
  padding: 20px 15px;
  margin-bottom: 20px;
  background-color: #007bff;
}

div.search-input input {
  border-radius: 6px;
  width: 100%;
  padding: 10px;
}